import React from "react";
import "./TopAchievers.css";
import { Link } from "react-router-dom";

const TopAchievers = () => {
  const achievers = [
    {
      image: "https://i.pinimg.com/736x/44/aa/99/44aa998d09afa45bbd489f3af6fdb7f4.jpg",
      title: "User of the Month",
      description:
        "Recognizing outstanding contributions, our User of the Month has demonstrated exemplary engagement and creativity in their interactions with EmiBot. Their thoughtful insights are truly appreciated.",
      linkText: "View Profile",
      linkTo: "/user-of-month"
    },
    {
      image: "https://m.media-amazon.com/images/I/71R-MICOBZL._UF894,1000_QL80_.jpg",
      title: "Most Helpful User",
      description:
        "This award goes to the user who has consistently provided valuable insights and assistance to others. Their willingness to share knowledge makes the EmiBot community stronger.",
      linkText: "Say Thanks",
      linkTo: "/thank-you"
    },
    {
      image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTR3fFC-zAIyAoeKfflp-iW_yEDhfSgM0Cd2w&s",
      title: "Community Leader",
      description:
        "Our Community Leader has taken the initiative to foster connections and create engaging activities. Their leadership inspires others to participate and collaborate within the community.",
      linkText: "Connect",
      linkTo: "/community-leader-profile"
    }
  ];

  return (
    <div className="top-achievers">
      <h2 className="main-title" style={{ color: "white" }}>Top Achievers</h2>
      <p className="subtitle" style={{ color: "wheat" }}>
        Celebrating the best of EmiBot community!
      </p>

      <div className="achievers-grid">
        {achievers.map((item, i) => (
          <div className="achiever-card" key={i}>
            <img
              src={item.image}
              alt={item.title}
              className="achiever-image"
              loading="lazy"
            />
            <h3 className="achiever-title">{item.title}</h3>
            <p className="achiever-desc">{item.description}</p>
            <Link to={item.linkTo} className="achiever-btn">
              {item.linkText}
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
};

export default TopAchievers;
